@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@font-face {
  font-family: "MeltedMonster";
  src: url("/MovieApp/static/media/Melted Monster.e388d190.ttf");
  font-weight: normal;
}
:root {
  --primary: #032541;
  --secondary: #21d07a;
  --added: #ffff29;
  --remove: #f81313;
  --white: #f2ff4d;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

.header {
  display: grid;
  grid-template: 1fr/auto 1fr auto auto auto minmax(10px, 10px);
  max-height: 2rem;
  padding: 0.15em 0.15em;
}
@media (max-width: 767px) {
  .header {
    font-size: calc(16px + 13.6 * ((100vw - 320px) / 1180));
  }
}
@media (min-width: 767px) {
  .header {
    font-size: calc(16px + 8 * (100vw / 1180));
  }
}
.header span {
  margin: 0 0.25rem 0 0;
  opacity: 0.75;
}
.header .fa-imdb {
  vertical-align: middle;
}
.header .header__button {
  padding: 0 0.5em 0.5rem 0;
  display: flex;
  align-items: center;
  min-height: 2rem;
  text-shadow: 1px 1px 2px #c54040, -1px -1px #c74b16;
}
@media (max-width: 767px) {
  .header .header__button {
    font-size: calc(12px + 6.8 * ((100vw - 320px) / 1180));
  }
}
@media (min-width: 767px) {
  .header .header__button {
    font-size: calc(12px + 4 * (100vw / 1180));
  }
}

.movie_card {
  height: 100%;
}

.handler {
  position: relative;
}
.handler:hover .movie {
  background-color: white;
  color: black;
}
.handler:hover .single_movie__icon {
  opacity: 0.8;
  text-shadow: 3px 3px 10px #272634;
}

@media (min-width: 991px) {
  .single_movie__icon {
    position: absolute;
    z-index: 5;
    left: 0.45em;
    opacity: 0.05;
    top: 0.45em;
    height: 50px;
  }

  .single_movie__icon.fa-bookmark,
.single_movie__icon.fa-bookmark-o,
.single_movie__icon.fa-times-circle {
    left: 1.85em;
  }

  .content_focus__icon:hover, .single_movie__icon:hover {
    color: #21d07a;
    color: var(--secondary);
  }

  .content_focus__icon.fa:hover, .single_movie__icon.fa:hover {
    color: #21d07a;
    color: var(--secondary);
  }
  .content_focus__icon.fa-bookmark, .single_movie__icon.fa-bookmark {
    color: #ffff29;
    color: var(--added);
  }
  .content_focus__icon.fa-bookmark:hover, .content_focus__icon.fa-eye-slash:hover, .single_movie__icon.fa-bookmark:hover, .single_movie__icon.fa-eye-slash:hover {
    color: #f81313;
    color: var(--remove);
  }

  .watch_page__icon.fa-times-circle:hover {
    color: #f81313;
    color: var(--remove);
  }
}
@media (max-width: 991px) {
  .single_movie__icon {
    display: none;
  }

  .content_focus__icon {
    position: relative;
  }
}
.header__title {
  margin: 0 auto;
}

.movies {
  display: grid;
  grid-gap: 15px;
  gap: 15px;
  margin: 0.5rem;
  grid-template-columns: repeat(auto-fit, minmax(190px, 0.3fr));
}

.page__movies, .page__title, .page__tv, .page__search {
  font-family: "Montserrat", sans-serif;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  font-size: 4vw;
  opacity: 0.8;
  text-shadow: 0px 0px 0.3em #ffffffda, 0px 0px 0.15em #ffffffda;
  mix-blend-mode: screen;
}

.page__search {
  font-size: 2vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.genres__holder {
  padding: 6px 0;
}

.search__container {
  display: flex;
  margin: 15px 0;
}

.content__wrapper {
  color: white;
  width: 90%;
  max-width: 60em;
  border: 1px solid #282c34;
  height: 95%;
  padding: 8px 8px 8px;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  background-color: #39445a;
}

.MuiPaginationItem-root {
  color: white !important;
}

.watchlist__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.watchlist__header_text {
  letter-spacing: 1px;
  padding: 0 1em;
  font-family: "Amatic";
  font-weight: bold;
}
@media (max-width: 767px) {
  .watchlist__header_text {
    font-size: calc(14px + 10.2 * ((100vw - 320px) / 1180));
  }
}
@media (min-width: 767px) {
  .watchlist__header_text {
    font-size: calc(14px + 6 * (100vw / 1180));
  }
}

.no-movies {
  max-width: 50rem;
  text-align: center;
  opacity: 0.85;
  display: block;
  font-family: "MeltedMonster";
  margin: 3rem auto;
  text-shadow: 1px 1px 2px #908888, -1px -1px 15px #908888;
}
@media (max-width: 767px) {
  .no-movies {
    font-size: calc(30px + 54.4 * ((100vw - 320px) / 1180));
  }
}
@media (min-width: 767px) {
  .no-movies {
    font-size: calc(30px + 32 * (100vw / 1180));
  }
}

.count_pill {
  display: block;
  background-color: #21d07a;
  background-color: var(--secondary);
  padding: 5px 15px;
  font-size: 1.125rem;
  font-weight: 600;
  border-radius: 999px;
  color: #032541;
  color: var(--primary);
}

.movie__card_control > .handler > .movie_card > .movie {
  grid-template: 0px minmax(10px, auto)/1fr;
  padding: 0;
}

@media (max-width: 767px) {
  .movies > div > button > span > span {
    font-size: calc(10px + 3.4 * ((100vw - 320px) / 1180));
  }
}
@media (min-width: 767px) {
  .movies > div > button > span > span {
    font-size: calc(10px + 2 * (100vw / 1180));
  }
}

@media (max-width: 991px) {
  .movies {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }
}
@media (max-width: 991px) and (max-width: 767px) {
  .movies .title {
    font-size: calc(12px + 6.8 * ((100vw - 320px) / 1180));
  }
}
@media (max-width: 991px) and (min-width: 767px) {
  .movies .title {
    font-size: calc(12px + 4 * (100vw / 1180));
  }
}
@media (max-width: 991px) and (max-width: 767px) {
  .movies .subtitle {
    font-size: calc(10px + 6.8 * ((100vw - 320px) / 1180));
  }
}
@media (max-width: 991px) and (min-width: 767px) {
  .movies .subtitle {
    font-size: calc(10px + 4 * (100vw / 1180));
  }
}
@media (max-width: 991px) {
  .content__wrapper {
    width: 85%;
    max-height: 50em;
  }
}
@media (max-width: 991px) and (max-width: 767px) {
  .content__tag {
    font-size: calc(12px + 6.8 * ((100vw - 320px) / 1180));
  }
}
@media (max-width: 991px) and (min-width: 767px) {
  .content__tag {
    font-size: calc(12px + 4 * (100vw / 1180));
  }
}
@media (max-width: 991px) and (max-width: 767px) {
  .content__description {
    font-size: calc(14px + 3.4 * ((100vw - 320px) / 1180));
  }
}
@media (max-width: 991px) and (min-width: 767px) {
  .content__description {
    font-size: calc(14px + 2 * (100vw / 1180));
  }
}
@media (max-width: 991px) {
  .carousel__item_text {
    font-size: 0.85em;
    line-height: 1em;
    text-transform: capitalize;
    font-weight: 400;
  }
}

